import { Injectable } from '@angular/core';
import { Jsonp } from '@angular/http';
import 'rxjs/Rx';

@Injectable()
export class MovieService {

  apiKey:string;

  constructor(private _jsonp:Jsonp) { 
    this.apiKey='35b7e0a85c93bed68524b2677e7e7ca6';


  }




  getPopular(){
    return this._jsonp.get('https://api.themoviedb.org/3/discover/movie?callback=JSONP_CALLBACK&sort_by=popularity.desc&api_key=35b7e0a85c93bed68524b2677e7e7ca6').map(res => res.json());
  }

  getInTheater(currentDate : string,futureDate : string){
    return this._jsonp.get('https://api.themoviedb.org/3/discover/movie?callback=JSONP_CALLBACK&primary_release_date.gte='+currentDate+'&primary_release_date.lte='+futureDate+'&api_key=35b7e0a85c93bed68524b2677e7e7ca6').map(res => res.json());
   
    
  }

  searchMovies(searchStr :string){

    return this._jsonp.get('https://api.themoviedb.org/3/search/movie?callback=JSONP_CALLBACK&query='+searchStr+'&sort_by=popularity.desc&api_key='+this.apiKey).map(res => res.json());
    
  }

  getMovie(id : string){

    return this._jsonp.get('https://api.themoviedb.org/3/movie/'+id+'?callback=JSONP_CALLBACK&api_key='+this.apiKey).map(res => res.json());

  }

}
  
  



