import { Component, OnInit } from '@angular/core';
import { MovieService } from '../services/movie.service';


@Component({
  selector: 'en-movies',
  templateUrl: './movies.component.html',
  styleUrls: ['./movies.component.css']
})
export class MoviesComponent implements OnInit {

  popularList : Array<Object>;
  inTheaters : Array<Object>;
  searchResult : Array<Object>;
  searchStr: string;
  day: number;
  month: number;
  year:number;
  today: string;

  addDays;
  addDay :number;
  addMonth :number;
  addYear:number;
  futureDate :string;
 
  constructor(private _moviesService:MovieService) {

    this.day = new Date().getUTCDate();
    this.month=new Date().getMonth()+1;
    this.year = new Date().getFullYear();    
    this.today =this.year +"-"+ this.month +"-"+ this.day;
    if(this.month<10){
      this.today =this.year +"-"+ this.month +"-"+ this.day;     
    }


    this.addDays=new Date();
    this.addDays.setDate(this.addDays.getUTCDate()+ 30);

    this.addDay=this.addDays.getUTCDate();
    this.addMonth=this.addDays.getMonth()+1;
    this.addYear=this.addDays.getFullYear();
    this.futureDate =this.addYear+'-'+this.addMonth+'-'+this.addDay;

    if(this.addMonth <10){
      this.futureDate =this.addYear+'-'+this.addMonth+'-'+this.addDay;
    }

    this._moviesService.getPopular().subscribe(res=> {
      this.popularList=res.results;
    });

    this._moviesService.getInTheater(this.today,this.futureDate).subscribe(res=> {
      this.inTheaters=res.results;
    });

   



   }

  ngOnInit() {
    
    
   
  }

  searchMovies(){
    this._moviesService.searchMovies(this.searchStr).subscribe(res=> {
      this.searchResult=res.results;
      
      });
  } 


}



